@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,600|Roboto%20Mono);
* {
  box-sizing: border-box; }

html, body {
  height: 100%;
  width: 100%; }

body {
  margin: 0;
  background-color: white;
  font: 400 1em "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4; }

.buttons {
  display: flex; }
  .buttons .button {
    flex: 1 1; }

.button {
  background-color: white;
  outline: none;
  border: none;
  color: #3d3d3d; }
  .button:disabled {
    opacity: .6; }
  .button.round {
    width: 40px;
    height: 40px;
    font-size: 1.5em;
    border-radius: 50%; }
    .button.round.big {
      width: 80px;
      height: 80px;
      font-size: 2.5em; }
      .button.round.big.play {
        padding-left: 0.35em;
        padding-top: 0.15em; }

.main-menu {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  padding: 100px 0;
  height: 100%;
  box-sizing: border-box;
  flex-direction: column;
  text-align: center; }
  .main-menu li {
    flex: 1 1;
    justify-content: center;
    display: flex;
    flex-direction: row;
    outline: none; }
    .main-menu li div {
      flex: 1 1;
      display: inline;
      align-self: center; }
      .main-menu li div span {
        font-size: 2em;
        color: rgba(0, 0, 0, 0.7); }
    .main-menu li.selected div span {
      color: #2891ff;
      border-bottom: 3px solid #2891ff; }

.board {
  width: 500px;
  height: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  border: solid 2px black; }
  @media (max-width: 500px) {
    .board {
      width: 90%;
      height: 90%; } }
  .board .board-line {
    flex: 1 1;
    display: flex; }
    .board .board-line .board-cell {
      flex: 1 1;
      display: flex;
      justify-content: center;
      align-items: center;
      font: 300 2.3em "Source Sans Pro", "Helvetica Neue", Arial, sans-serif;
      color: black;
      background-color: white;
      border-bottom: solid 1px #bec6d4; }
      .board .board-line .board-cell:hover:not(.disabled) {
        cursor: pointer; }
      .board .board-line .board-cell:not(:last-child) {
        border-right: solid 1px #bec6d4; }
      .board .board-line .board-cell:nth-of-type(3), .board .board-line .board-cell:nth-of-type(6) {
        border-right: solid 2px black; }
      .board .board-line .board-cell.highlighted {
        background-color: #f5f5f5; }
      .board .board-line .board-cell.selected {
        background-color: gainsboro; }
      .board .board-line .board-cell.focused {
        background-color: #d0e7ff; }
      .board .board-line .board-cell.error {
        color: #ff4c4c; }
      .board .board-line .board-cell:not(.disabled):not(.error) {
        color: #0077f4; }
    .board .board-line:nth-of-type(3) .board-cell, .board .board-line:nth-of-type(6) .board-cell {
      border-bottom: solid 2px black; }
    .board .board-line:nth-of-type(4) .board-cell, .board .board-line:nth-of-type(7) .board-cell {
      border-top: none; }
    .board .board-line:nth-of-type(9) .board-cell {
      border-bottom: none; }

.panel-buttons {
  display: flex;
  width: 500px;
  margin: 20px auto 0;
  background-color: white;
  font: 300 2.3em "Source Sans Pro", "Helvetica Neue", Arial, sans-serif; }
  @media (max-width: 500px) {
    .panel-buttons {
      width: 90%; } }
  .panel-buttons .panel-button {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
    border-bottom: solid 1px #bec6d4;
    border-right: solid 1px #bec6d4;
    color: black; }
    .panel-buttons .panel-button:hover {
      cursor: pointer;
      background-color: #d0e7ff;
      color: #0077f4; }
    .panel-buttons .panel-button:last-child {
      border-right: none; }
  .panel-buttons .panel-numbers {
    flex: 2 1;
    display: flex;
    flex-direction: column;
    border: solid 2px black; }
    .panel-buttons .panel-numbers .numbers {
      align-self: stretch;
      display: flex; }
      .panel-buttons .panel-numbers .numbers:last-child .number {
        border-bottom: none; }
  .panel-buttons .panel-help {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    border: solid 2px black;
    border-left: none; }
    .panel-buttons .panel-help .panel-row {
      flex: 1 1;
      display: flex; }
      .panel-buttons .panel-help .panel-row:last-child {
        border-top: solid 1px #bec6d4; }
      .panel-buttons .panel-help .panel-row .panel-button {
        flex: 1 1;
        display: flex;
        justify-content: center;
        align-items: center;
        border: none; }
        .panel-buttons .panel-help .panel-row .panel-button + .panel-button {
          border-left: solid 1px #bec6d4; }
        .panel-buttons .panel-help .panel-row .panel-button.button-new-game {
          color: white;
          background-color: #2891ff;
          border-top-color: #2891ff; }

.options {
  width: 500px;
  margin: 0 auto; }
  @media (max-width: 500px) {
    .options {
      width: 90%; } }
  .options .buttons {
    margin: 20px auto 10px;
    display: flex; }
    .options .buttons .button-option {
      flex: 1 1;
      text-align: left;
      position: relative; }
      .options .buttons .button-option .menu-options {
        position: absolute;
        list-style: none;
        padding: 10px;
        margin: 0;
        background-color: #0131c2;
        color: white;
        border-radius: 5px;
        margin-top: 15px;
        -webkit-transition: .2s ease-in all;
        transition: .2s ease-in all;
        opacity: 0;
        pointer-events: none; }
        .options .buttons .button-option .menu-options.active {
          opacity: 1;
          pointer-events: inherit; }
        .options .buttons .button-option .menu-options:before {
          content: "";
          position: absolute;
          left: 11px;
          top: -8px;
          width: 0;
          height: 0;
          border-style: solid;
          border-width: 0 8px 8px 8px;
          border-color: transparent transparent #0131c2 transparent;
          z-index: 9999; }
        .options .buttons .button-option .menu-options > li {
          padding: 5px; }
          .options .buttons .button-option .menu-options > li:hover {
            cursor: pointer; }
            .options .buttons .button-option .menu-options > li:hover > span {
              opacity: .8; }
          .options .buttons .button-option .menu-options > li > span {
            opacity: .6; }
          .options .buttons .button-option .menu-options > li.active {
            font-weight: bold; }
            .options .buttons .button-option .menu-options > li.active::before {
              content: '\2713';
              margin-right: 5px; }
            .options .buttons .button-option .menu-options > li.active > span {
              opacity: 1; }
      .options .buttons .button-option:last-child {
        text-align: right; }
      .options .buttons .button-option .button:hover {
        cursor: pointer;
        opacity: .8; }

